.login-wrap {
  width: 100%;
  position: relative;
  background-color: #fff;
  height: calc(100vh - 230px);
}
.login-wrap div.chef-lee-background {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 20px;
  width: 140%;
}
.login-wrap div.chef-lee-background img {
  width: 100%;
  height: auto;
  -o-object-fit: cover;
     object-fit: cover;
  background-size: cover;
  background-repeat: no-repeat;
}
.login-wrap div.login-component {
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.login-wrap div.login-component h4 {
  font-weight: 900;
  color: #2E483E;
  width: 500px;
  margin: 0px auto;
  padding: 20px;
  font-size: 25px;
}
.login-wrap div.login-component form {
  width: 500px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin: 0px auto;
}
.login-wrap div.login-component form input {
  width: 100%;
  height: 60px;
  border-radius: 15px;
  border: 1px solid #454545;
  margin-bottom: 10px;
  padding-left: 30px;
}
.login-wrap div.login-component form .submit-btn {
  background-color: #55A06A;
  border: none;
  width: 100%;
  margin-top: 20px;
  color: #fff;
  font-weight: 700;
  font-size: 19px;
}
.login-wrap div.login-component form a {
  padding: 10px;
}
.login-wrap div.login-component form b {
  color: #2E483E;
  font-weight: 500;
  position: relative;
}
.login-wrap div.login-component form b::after {
  content: "";
  width: 100%;
  height: 1.5px;
  background-color: #2E483E;
  position: absolute;
  left: 50%;
  top: 110%;
  transform: translateX(-50%);
}
.login-wrap div.login-component .google-login-btn {
  margin: 50px auto 0px auto;
}
.login-wrap div.login-component .google-login-btn button {
  width: 500px;
  height: 60px;
  background-color: transparent;
  border: 2px solid #9b9b9b;
  border-radius: 50px;
  display: flex;
  align-items: center;
}
.login-wrap div.login-component .google-login-btn button p {
  font-size: 18px;
  font-weight: 600;
  color: #575757;
  margin: 0px auto;
}
.login-wrap div.login-component .google-login-btn img {
  width: 30px;
  height: 30px;
  -o-object-fit: cover;
     object-fit: cover;
  background-size: cover;
  background-repeat: no-repeat;
  margin-left: 15px;
  margin: 0px -25px 0px 10px;
}/*# sourceMappingURL=Login.css.map */